.ss-app-config-btn {
    &[disabled] {
        background-color: lighten(@gray4, 25%);
        border-color: @gray4;
        color: @gray6;

        &:hover {
            background-color: lighten(@gray4, 10%);
            color: @gray6;
        }

        svg[class*="ss-app-config-ico"] {
            &.stroke-ico {
                stroke: @gray6;
            }
            &.fill-ico {
                fill: @gray6;
                .fill-path {
                    stroke: none;
                    fill: @gray6;
                }
            }
        }
    }

    &.ss-app-config-btn-primary[disabled] {
        background-color: lighten(@blue, 25%);
        border-color: lighten(@blue, 25%);
        color: @white;

        &:hover {
            background-color: lighten(@blue, 10%);
            color: @white;
        }

        svg[class*="ss-app-config-ico"] {
            &.stroke-ico {
                stroke: @white;
            }
            &.fill-ico {
                fill: @white;
                .fill-path {
                    stroke: none;
                    fill: @white;
                }
            }
        }
    }

    // Colors
    &.light-blue {
        background-color: @light-blue;
        color: @white;
        border-color: @light-blue;

        &:hover {
            background-color: darken(@light-blue, 10%);
            border-color: darken(@light-blue, 10%);
            color: @white;
        }

        &[disabled] {
            background-color: lighten(@light-blue, 25%);
            border-color: lighten(@light-blue, 25%);

            &:hover {
                background-color: lighten(@light-blue, 10%);
            }
        }
    }
}
